"use client"

import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Textarea } from "@/components/ui/textarea"
import { Edit, Plus, Upload } from "lucide-react"
import Image from "next/image"
import { useState } from "react"

// Sample branches data
const branches = [
  {
    id: 1,
    name: "Kantor Pusat",
    code: "HO",
    address: "Jl. Utama No. 123",
    phone: "021-5551234",
    email: "ho@company.com",
    manager: "John Doe",
    status: "Active",
  },
  {
    id: 2,
    name: "Cabang Jakarta Selatan",
    code: "JKS",
    address: "Jl. Kemang Raya No. 45",
    phone: "021-7778888",
    email: "jaksel@company.com",
    manager: "Jane Smith",
    status: "Active",
  },
]

export default function BranchesPage() {
  const [showNewBranch, setShowNewBranch] = useState(false)
  const [showEditBranch, setShowEditBranch] = useState(false)
  const [selectedBranch, setSelectedBranch] = useState<any>(null)
  const [branchLogo, setBranchLogo] = useState<string | null>(null)

  const handleLogoUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      const reader = new FileReader()
      reader.onloadend = () => {
        setBranchLogo(reader.result as string)
      }
      reader.readAsDataURL(file)
    }
  }

  const handleEditBranch = (branch: any) => {
    setSelectedBranch(branch)
    setShowEditBranch(true)
  }

  return (
    <div className="flex flex-col gap-6">
      <div className="flex justify-between items-center">
        <h1 className="text-3xl font-bold">Manajemen Cabang</h1>
        <Dialog open={showNewBranch} onOpenChange={setShowNewBranch}>
          <DialogTrigger asChild>
            <Button>
              <Plus className="mr-2 h-4 w-4" />
              Tambah Cabang
            </Button>
          </DialogTrigger>
          <DialogContent className="sm:max-w-[600px]">
            <DialogHeader>
              <DialogTitle>Tambah Cabang Baru</DialogTitle>
            </DialogHeader>
            <form className="grid gap-4 py-4">
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="name">Nama Cabang</Label>
                  <Input id="name" placeholder="Nama cabang" required />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="code">Kode Cabang</Label>
                  <Input id="code" placeholder="Kode cabang" required />
                </div>
              </div>

              <div className="space-y-2">
                <Label htmlFor="address">Alamat</Label>
                <Textarea id="address" placeholder="Alamat cabang" required />
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="phone">Telepon</Label>
                  <Input id="phone" type="tel" placeholder="Nomor telepon" required />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="email">Email</Label>
                  <Input id="email" type="email" placeholder="Email cabang" required />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="manager">Manager Cabang</Label>
                  <Input id="manager" placeholder="Nama manager" required />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="status">Status</Label>
                  <Select>
                    <SelectTrigger>
                      <SelectValue placeholder="Pilih status" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="active">Active</SelectItem>
                      <SelectItem value="inactive">Inactive</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>

              <div className="space-y-2">
                <Label>Logo Cabang</Label>
                <div className="flex flex-col items-center justify-center border-2 border-dashed rounded-lg p-6">
                  {branchLogo ? (
                    <div className="relative w-32 h-32">
                      <Image
                        src={branchLogo}
                        alt="Branch Logo"
                        fill
                        className="object-contain"
                      />
                    </div>
                  ) : (
                    <div className="text-center">
                      <Upload className="mx-auto h-8 w-8 text-gray-400" />
                      <p className="mt-2 text-sm text-gray-600">
                        Upload logo cabang
                      </p>
                      <p className="text-xs text-gray-500">
                        PNG, JPG, GIF (max. 2MB)
                      </p>
                    </div>
                  )}
                  <input
                    type="file"
                    id="branch_logo"
                    accept="image/*"
                    className="hidden"
                    onChange={handleLogoUpload}
                  />
                  <Button
                    type="button"
                    variant="outline"
                    className="mt-4"
                    onClick={() => document.getElementById("branch_logo")?.click()}
                  >
                    {branchLogo ? "Ganti Logo" : "Pilih File"}
                  </Button>
                </div>
              </div>

              <div className="flex justify-end gap-2">
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => setShowNewBranch(false)}
                >
                  Batal
                </Button>
                <Button type="submit">Simpan</Button>
              </div>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>Daftar Cabang</CardTitle>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Kode</TableHead>
                <TableHead>Nama Cabang</TableHead>
                <TableHead>Alamat</TableHead>
                <TableHead>Telepon</TableHead>
                <TableHead>Manager</TableHead>
                <TableHead>Status</TableHead>
                <TableHead className="text-right">Aksi</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {branches.map((branch) => (
                <TableRow key={branch.id}>
                  <TableCell className="font-medium">{branch.code}</TableCell>
                  <TableCell>{branch.name}</TableCell>
                  <TableCell>{branch.address}</TableCell>
                  <TableCell>{branch.phone}</TableCell>
                  <TableCell>{branch.manager}</TableCell>
                  <TableCell>
                    <span
                      className={`px-2 py-1 rounded-full text-xs ${
                        branch.status === "Active"
                          ? "bg-green-100 text-green-800"
                          : "bg-red-100 text-red-800"
                      }`}
                    >
                      {branch.status}
                    </span>
                  </TableCell>
                  <TableCell className="text-right">
                    <Button
                      variant="ghost"
                      size="icon"
                      onClick={() => handleEditBranch(branch)}
                    >
                      <Edit className="h-4 w-4" />
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>

      {/* Edit Branch Dialog */}
      <Dialog open={showEditBranch} onOpenChange={setShowEditBranch}>
        <DialogContent className="sm:max-w-[600px]">
          <DialogHeader>
            <DialogTitle>Edit Cabang</DialogTitle>
          </DialogHeader>
          {selectedBranch && (
            <form className="grid gap-4 py-4">
              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="edit-name">Nama Cabang</Label>
                  <Input
                    id="edit-name"
                    defaultValue={selectedBranch.name}
                    required
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="edit-code">Kode Cabang</Label>
                  <Input
                    id="edit-code"
                    defaultValue={selectedBranch.code}
                    required
                  />
                </div>
              </div>

              <div className="space-y-2">
                <Label htmlFor="edit-address">Alamat</Label>
                <Textarea
                  id="edit-address"
                  defaultValue={selectedBranch.address}
                  required
                />
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="edit-phone">Telepon</Label>
                  <Input
                    id="edit-phone"
                    type="tel"
                    defaultValue={selectedBranch.phone}
                    required
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="edit-email">Email</Label>
                  <Input
                    id="edit-email"
                    type="email"
                    defaultValue={selectedBranch.email}
                    required
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div className="space-y-2">
                  <Label htmlFor="edit-manager">Manager Cabang</Label>
                  <Input
                    id="edit-manager"
                    defaultValue={selectedBranch.manager}
                    required
                  />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="edit-status">Status</Label>
                  <Select defaultValue={selectedBranch.status.toLowerCase()}>
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="active">Active</SelectItem>
                      <SelectItem value="inactive">Inactive</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              </div>

              <div className="space-y-2">
                <Label>Logo Cabang</Label>
                <div className="flex flex-col items-center justify-center border-2 border-dashed rounded-lg p-6">
                  {branchLogo ? (
                    <div className="relative w-32 h-32">
                      <Image
                        src={branchLogo}
                        alt="Branch Logo"
                        fill
                        className="object-contain"
                      />
                    </div>
                  ) : (
                    <div className="text-center">
                      <Upload className="mx-auto h-8 w-8 text-gray-400" />
                      <p className="mt-2 text-sm text-gray-600">
                        Upload logo cabang
                      </p>
                      <p className="text-xs text-gray-500">
                        PNG, JPG, GIF (max. 2MB)
                      </p>
                    </div>
                  )}
                  <input
                    type="file"
                    id="edit_branch_logo"
                    accept="image/*"
                    className="hidden"
                    onChange={handleLogoUpload}
                  />
                  <Button
                    type="button"
                    variant="outline"
                    className="mt-4"
                    onClick={() => document.getElementById("edit_branch_logo")?.click()}
                  >
                    {branchLogo ? "Ganti Logo" : "Pilih File"}
                  </Button>
                </div>
              </div>

              <div className="flex justify-end gap-2">
                <Button
                  type="button"
                  variant="outline"
                  onClick={() => setShowEditBranch(false)}
                >
                  Batal
                </Button>
                <Button type="submit">Simpan</Button>
              </div>
            </form>
          )}
        </DialogContent>
      </Dialog>
    </div>
  )
}
